import "./HomePage.css"

import {
    Link
} from "react-router-dom";

import { FadeIn, HomePageLogoEffect1, HomePageLogoEffect2, HomePageLogoEffect3, HomePageLogoEffect4, HomePageLogoEffect5,
        HomePageLogoEffect6, HomePageLogoEffect7, HomePageLogoEffect8, HomePageLogoEffect9, HomePageLogoEffect10, HomePageLogoLineEffect} from "../../../utility"


const HomePage = () => {
    return (
        <FadeIn>
            <div>
             
                <h1>hoi</h1>
                
                <Link
                        className=''
                        style={
                            {
                                textDecoration: 'none',
                            }
                        }
                        to="/projects"
                        // onMouseEnter={() => setHoverStatusGoBack(true)}
                        // onMouseLeave={() => setHoverStatusGoBack(false)}
                    >
                        go to projects here
                </Link>
                <div id='homepage-logo-container'>
                    <HomePageLogoEffect1><h1 id="letter">H</h1></HomePageLogoEffect1>
                    <HomePageLogoEffect2><h1 id="letter">e</h1></HomePageLogoEffect2>
                    <HomePageLogoEffect3><h1 id="letter">l</h1></HomePageLogoEffect3>
                    <HomePageLogoEffect4><h1 id="letter">l</h1></HomePageLogoEffect4>
                    <HomePageLogoEffect5><h1 id="letter">o</h1></HomePageLogoEffect5>

                    <HomePageLogoEffect5><h1 id="letter"> </h1></HomePageLogoEffect5>
                    
                    <HomePageLogoEffect6><h1 id="letter">W</h1></HomePageLogoEffect6>
                    <HomePageLogoEffect7><h1 id="letter">o</h1></HomePageLogoEffect7>
                    <HomePageLogoEffect8><h1 id="letter">r</h1></HomePageLogoEffect8>
                    <HomePageLogoEffect9><h1 id="letter">l</h1></HomePageLogoEffect9>
                    <HomePageLogoEffect10><h1 id="letter">d</h1></HomePageLogoEffect10>
                    {/* <div id='line-one'></div>
                    <div id='line-two'></div> */}
                </div>
                    <HomePageLogoLineEffect><div id='line-one' className='line'></div></HomePageLogoLineEffect>
                    <HomePageLogoLineEffect><div id='line-two' className='line'></div></HomePageLogoLineEffect>
                
            </div>
        </FadeIn>
    )
}

export default HomePage
